.swiper-zoom-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    text-align: center;
    > img,
    > svg,
    > canvas {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
    }
}
.swiper-slide-zoomed {
    cursor: move;
}
