/* ======================================================
    <!-- 3D Background -->
/* ====================================================== */
@import '@uixkit/core/_global/scss/_variable-and-mixin.scss';
.uix-3d-bg {
    /* Require more than 500, compatible firefox height */
    perspective: 700px;
    > div {
        @include transition-default();
        display: inline-block;
    }
    img {
        @include transition-default();
    }
}
